import React, { useState, useEffect } from 'react';
import { ImHappy, ImSad } from 'react-icons/im';
import Search from '../../../widgets/Search/Search';
import { BsGearFill } from 'react-icons/bs';

import s from './Layout.module.scss';
import Settings from '../Settings/Settings';

const Layout = ({
    user, left, area, right
}) => {

    const [showSettings, setShowSettings] = useState(false);
    user = Boolean(user) ? user : {};
    return <main className={s.main}>
        <Settings 
            show={showSettings} name={user.name} 
            onClose={() => setShowSettings(false)}
            id={user.id} 
        />
        <header className={s.main_header}>
            <div>
                Hii {user.name || user.username} 👋
            </div>
            <div>
                Total Queries Responded: {user.answered}
            </div>
            <Search />
            <BsGearFill onClick={() => setShowSettings(s => !s)} />
            <div>
                <div>
                    <ImHappy color='#0a0' />
                    {user.satisfied}
                </div>
                <div>
                    <ImSad color='#a00' />
                    {user.dissatisfied}
                </div>
            </div>
        </header>
        <aside className={s.main_side}>
            {left}
        </aside>
        <section className={s.main_area}>
            {area}
        </section>
        <div className={s.main_right}>
            {right}
        </div>
    </main>;
};

export default Layout;