import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { updateAccount } from '../../../actions';
import Form from '../../../widgets/Form/Form';
import Modal from '../../../widgets/Modal/Modal';
import { useWarning } from '../../../widgets/Warning/Warning';

const STYLES = { 
    width: '35vw', display: 'flex', 
    alignItems: 'center', height: '85vh' 
};

const DEFAULT_DATA = { name: '', password: '', newPassword: '' };
const Settings = ({
    show, onClose, dispatch, id,
    name, updateAccountReducer
}) => {

    const [error, setError] = useWarning('');
    const [canSubmit, setCanSubmit] = useState(true);
    const [data, setData] = useState(DEFAULT_DATA);
    useEffect(() => setData(d => ({ ...d, name })), [name]);
    const update = () => {
        setCanSubmit(false);
        dispatch(updateAccount({...data, id}));
    };
    useEffect(() => console.log(error), [error]);
    useEffect(() => {
        if(!updateAccountReducer) return;
        setCanSubmit(true);
        const { success, message } = updateAccountReducer;
        if(!success) setError(message);
        else onClose();
    }, [updateAccountReducer]);
    return <Modal
        show={show} 
        onClose={onClose}
        style={{ content: STYLES }}
    >
        <Form 
            heading='Executive Settings' cta='Apply Changes'
            format={[
                {
                    type: 'text', uid: 'fullname',
                    onChange: e => setData(d => ({ ...d, name: e })),
                    value: data.name, placeholder: 'Full Name'
                },
                {
                    type: 'password', uid: 'CurrentPass',
                    onChange: e => setData(d => ({ ...d, password: e })),
                    value: data.password, placeholder: 'Current Password'
                },
                {
                    type: 'password', uid: 'NewPass',
                    onChange: e => setData(d => ({ ...d, newPassword: e })),
                    value: data.newPassword, placeholder: 'New Password'
                },
            ]} canSubmit={canSubmit} onSubmit={update} error={error}
        />
    </Modal>
};

const matchStateToProps = (state = {}) => {
    const { updateAccountReducer } = state;
    return { updateAccountReducer };
};

export default connect(matchStateToProps)(Settings);